import styled from "styled-components";

const ListItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 20px;
  border-top: 1px solid #eee;
  white-space: pre;
  user-select: none;
`;

export default ListItem;



